import { Injectable, OnDestroy } from '@angular/core';

import { interval, timer, Subscription } from 'rxjs';
import * as moment from 'moment';
import { KeycloakService } from 'keycloak-angular';

import { ENVIRONMENT } from '../environments/environment';

@Injectable({
  providedIn: 'root',
})
export class AuthService implements OnDestroy {
  intervalKeycloakSubscription: Subscription;

  constructor(private keycloakService: KeycloakService) {}

  ngOnDestroy() {
    this.intervalKeycloakSubscription.unsubscribe();
  }

  getDataloginKeycloak() {
    if (ENVIRONMENT.keycloakActived) {
      const keycloakResponse = this.keycloakService.getKeycloakInstance();
      const tokenParsed = keycloakResponse['tokenParsed'];
      let dataUsr = { data: null, roles: [], permisos: [] };
      console.log(
        '::::: SUCCESS: KEYCLOAK ACTIVE - [ngDoBootstrap] bootstrap app',
        JSON.stringify(keycloakResponse)
      );

      //   if (!keycloakResponse.authenticated) {
      //     this.keycloakService.login();
      //   } else {
      this.interval(tokenParsed?.exp);
      //   }
    }
  }

  logoutKeycloak() {
    if (ENVIRONMENT.keycloakActived) {
      this.keycloakService.logout();
    }
  }

  updateToken() {
    // if (!this.keycloakService.isTokenExpired()) {
    this.intervalKeycloakSubscription.unsubscribe();

    setTimeout(() => {
      this.keycloakService
        .updateToken(5)
        .then((_s) => {
          setTimeout(() => {
            // this.interval(
            //   this.keycloakService.getKeycloakInstance()?.tokenParsed?.exp
            // );

            console.log(
              '::::: UPDATE TOKEN ',
              _s,
              JSON.stringify(this.keycloakService.getToken())
            );
          }, 2000);
        })
        .catch((_e) =>
          console.error(':::: ERROR UPDATE TOKEN ', JSON.stringify(_e))
        );
    }, 1000);
    // }
  }

  interval(exp) {
    const contador = interval(10000);
    this.intervalKeycloakSubscription = contador.subscribe((_count) => {
      const todayUnixDate = moment().unix();
      const timeExpToken = exp - todayUnixDate;

      if (timeExpToken <= 10) this.updateToken();

      console.log(`
        ###############################
        Count: ${_count},
        TokenExpUnixDate: ${exp}
        TodayUnixDate: ${todayUnixDate}
        TimeExpToken: ${timeExpToken} seconds
        IsTokenExpired(): ${this.keycloakService.isTokenExpired()}
        ###############################`);
    });
  }
}
