import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  AfterViewInit,
  OnChanges,
  OnDestroy,
  ViewChild,
} from '@angular/core';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';

import { NgbDateParserFormatter } from '@ng-bootstrap/ng-bootstrap';
import { NgbDateCustomParserFormatter } from '../../classes/NgbDateCustomParserFormatter';

// COMPONENTS
import { WidgetInputDateComponent } from '../widget-input-date/widget-input-date.component';

@Component({
  selector: 'app-bar-top-filters-tools',
  templateUrl: './bar-top-filters-tools.component.html',
  styleUrls: ['./bar-top-filters-tools.component.scss'],
  providers: [
    { provide: NgbDateParserFormatter, useClass: NgbDateCustomParserFormatter },
  ],
})
export class BarTopFiltersToolsComponent implements OnInit {
  @ViewChild('fechaInicioComp', { static: false })
  fechaInicioComp: WidgetInputDateComponent;
  @ViewChild('fechaFinComp', { static: false })
  fechaFinComp: WidgetInputDateComponent;

  groupForm: FormGroup;

  option1: any = [
    {
      label: 'Hoy',
      value: 'Hoy',
      id: 'HOY',
    },
  ];
  options2: any = [
    {
      label: 'Exportar',
      value: 'Exportar',
      id: 'EX',
    },
  ];

  constructor(private formBuilder: FormBuilder) {
    this.groupForm = this.formBuilder.group({
      fechaInicio: [{ value: null, disabled: false }, [Validators.required]],
      fechaFin: [{ value: null, disabled: false }, [Validators.required]],
    });
  }

  ngOnInit(): void {}

  get fc() {
    return this.groupForm.controls;
  }

  updateFechasEvent(DataSet, fcName) {
    console.log('::::: SELECCION FECHA: ', JSON.stringify(DataSet), fcName);

    this.groupForm.get(fcName).setValue(DataSet);
    this.groupForm.get(fcName).updateValueAndValidity();
    this.validacionPeriodo();
  }

  validacionPeriodo() {
    let fechaInicio = this.groupForm.get('fechaInicio').value;
    let fechaFin = this.groupForm.get('fechaFin').value;
    console.log(`:::::
    FECHA INICIO: ${fechaInicio}
    FECHA FIN:    ${fechaFin}
    FI > FF: ${fechaInicio > fechaFin}`);

    if (fechaInicio && fechaFin) {
      if (fechaInicio > fechaFin) {
        this.groupForm.get('fechaFin').setErrors({
          customError: 'La fecha debe ser mayor a la fecha De',
        });
        this.fechaFinComp.setCustomError(
          'La fecha debe ser mayor a la fecha De'
        );
      } else {
        this.groupForm.get('fechaFin').setErrors(null);
        this.fechaFinComp.setCustomError(null);
      }
    }
  }
}
