import { Injectable } from '@angular/core';
import {
  HttpEvent,
  HttpInterceptor,
  HttpHandler,
  HttpRequest,
  HttpResponse,
  HttpErrorResponse,
  HttpHeaders,
} from '@angular/common/http';
//  TERCEROS
import * as _ from 'lodash';
import * as uuid from 'uuid';
import { Observable } from 'rxjs';
import { tap } from 'rxjs/operators';
import { ToastrService } from 'ngx-toastr';
// ENVIRONMENTS
import { ENVIRONMENT } from '../environments/environment';

@Injectable()
export class InterceptorProvider implements HttpInterceptor {
  token: string;

  constructor(private toastrService: ToastrService) {}

  intercept(
    req: HttpRequest<any>,
    next: HttpHandler
  ): Observable<HttpEvent<any>> {
    // TODO: GET TOKEN
    const token = `Bearer eyJhbGciOiJSUzI1NiIsInR5cCIgOiAiSldUIiwia2lkIiA6ICJGQjJTUll2ZUFCZXhKbDNPb0hkN29rU2VOMXJpUG9lMjJ2OGFTWGotVTRZIn0..qwg8C48pvShWegmclvPMi-0dOn0pfeXG4FgjCsU-hATDucCp0YqG7kr14U22Rm7BwPPhRQ0GNlqkGF3cfIM5mbrsOiXE9GaePcfFTCkqtTg8366sEySxUx3JCeGKtBjtaWIvotbW2qJ8kggW--LpIXsrOkMqwKvEx-tzQYF1-Y7p0AU9CDcLJNc-bFlVZOm2MYg0nFPwHU29KUBfW4c_4jeUpmou-HL4ElDxVpx134wBKycH9m0VcdsZE_LUxEGvdcJa8Cx8ARLqgTv1UB_PgH92AOyTuVFa47MDK5Q8aPaVeVh7DNjgya7UihlpWdhVk6nbhF7jwdG7r3TeU5_DjA`;
    const uuid4 = req.method == 'POST' ? uuid.v4() : '';
    let dataBody = {};
    let update = {};

    if (ENVIRONMENT.keycloakActived) {
      update = {
        // headers?: HttpHeaders;
        // headers: new HttpHeaders().set("UUID", uuid.v4()),
        // reportProgress?: boolean;
        // params?: HttpParams;
        // responseType?: 'arraybuffer' | 'blob' | 'json' | 'text';
        // withCredentials: true,
        // body: req.body,
        // method?: string;
        // url?: string;
        setHeaders: {
          'Content-Type': 'application/json',
          UUID: uuid4,
          // Authorization: token
        },
        // setParams?: {
        //     [param: string]: string;
        // };
      };
    } else {
      update = {
        // headers?: HttpHeaders;
        // headers: new HttpHeaders().set("UUID", uuid.v4()),
        // reportProgress?: boolean;
        // params?: HttpParams;
        // responseType?: 'arraybuffer' | 'blob' | 'json' | 'text';
        // withCredentials: true,
        // body: req.body,
        // method?: string;
        // url?: string;
        setHeaders: {
          'Content-Type': 'application/json',
          UUID: uuid4,
          Authorization: token,
        },
        // setParams?: {
        //     [param: string]: string;
        // };
      };
    }

    _.assign(req.body, dataBody);

    const authReq = req.clone(update);

    console.log(':::::REQUEST: ', JSON.stringify(authReq));

    return next.handle(authReq).pipe(
      tap(
        (event) => {
          if (event instanceof HttpResponse) {
            if (event.body.errors)
              _.each(event.body.errors, (err) => {
                this.toastrService.error(err.message, err.title, {
                  timeOut: 3000,
                });
              });
          }
        },
        (error) => {
          if (error instanceof HttpErrorResponse) {
            this.toastrService.error(error.message, error.name, {
              timeOut: 3000,
            });
            console.log(
              ':::::INTERCEPTOR/RESPONSE/ERROR: ',
              JSON.stringify(error)
            );
          } else {
            return Observable.throw(error);
          }
        }
      )
    );
  }
}
