import { BrowserModule } from '@angular/platform-browser';
import { CommonModule, registerLocaleData } from '@angular/common';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { FormsModule } from '@angular/forms';
import {
  NgModule,
  DoBootstrap,
  ApplicationRef,
  LOCALE_ID,
} from '@angular/core';
import { ServiceWorkerModule } from '@angular/service-worker';

import localeSp from '@angular/common/locales/es';
registerLocaleData(localeSp, 'es');

import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { InterceptorProvider } from './interceptor';

import { KeycloakAngularModule, KeycloakService } from 'keycloak-angular';
const keycloakService = new KeycloakService();

import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { NgSelectModule } from '@ng-select/ng-select';
import { ToastrModule } from 'ngx-toastr';
import {
  NgxUiLoaderModule,
  NgxUiLoaderRouterModule,
  NgxUiLoaderHttpModule,
} from 'ngx-ui-loader';

// ROUTING
import { AppRoutingModule } from './app-routing.module';

// MODULES
import { SharedModule } from './shared/shared.module';
import { TemplateModule } from './template/template.module';
import { GraficasModule } from './graficas/graficas.module';

// COMPONENTS
import { AppComponent } from './app.component';
import { CommonLayoutComponent } from './common/common-layout.component';
import { DashboardComponent } from './dashboard/dashboard.component';

// ENVIRONMENTS
import { ENVIRONMENT } from '../environments/environment';
import { environment } from '../environments/environment';

@NgModule({
  declarations: [AppComponent, CommonLayoutComponent, DashboardComponent],
  imports: [
    BrowserModule,
    AppRoutingModule,
    CommonModule,
    BrowserAnimationsModule,
    NgbModule,
    NgSelectModule,
    FormsModule,
    //
    KeycloakAngularModule,
    ToastrModule.forRoot(),
    NgxUiLoaderModule,
    NgxUiLoaderRouterModule,
    NgxUiLoaderHttpModule.forRoot({
      showForeground: true,
      // exclude: configuracion.NOT_SHOW_LOADER_URLS
    }),
    // MODULES
    SharedModule,
    TemplateModule,
    GraficasModule,
    ServiceWorkerModule.register('ngsw-worker.js', {
      enabled: environment.production,
    }),
  ],
  providers: [
    { provide: LOCALE_ID, useValue: 'es' },
    {
      provide: KeycloakService,
      useValue: keycloakService,
    },
    {
      provide: HTTP_INTERCEPTORS,
      useClass: InterceptorProvider,
      multi: true,
    },
  ],
  entryComponents: [AppComponent],
})
export class AppModule implements DoBootstrap {
  constructor() {}
  ngDoBootstrap(appRef: ApplicationRef) {
    if (ENVIRONMENT.keycloakActived) {
      keycloakService
        .init({
          config: ENVIRONMENT.keycloak,
          initOptions: {
            onLoad: 'login-required',
            // onLoad: 'check-sso',
            checkLoginIframe: false,
            enableLogging: true,
            redirectUri: ENVIRONMENT.redirectUri,
          },
          //   bearerExcludedUrls: ['.*'],
          enableBearerInterceptor: true,
        })
        .then(() => {
          //   if (!keycloakService.getKeycloakInstance().authenticated) {
          //     keycloakService.login();
          //   } else {
          appRef.bootstrap(AppComponent);
          //   }
        })
        .catch((error) =>
          console.error(
            '::::: ERROR: KEYCLOAK - [ngDoBootstrap] init Keycloak failed',
            error
          )
        );
    } else {
      appRef.bootstrap(AppComponent);
    }
  }
}
