import { Component, OnInit, Input } from '@angular/core';
import { NgxChartsModule } from '@swimlane/ngx-charts';

@Component({
  selector: 'app-ngx-advanced-pie-chart',
  templateUrl: './ngx-advanced-pie-chart.component.html',
  styleUrls: ['./ngx-advanced-pie-chart.component.scss'],
})
export class NgxAdvancedPieChartComponent implements OnInit {
  @Input() data: any[];
  @Input() colors: any[];
  view: any[];
  //   cccc = (a) => {
  //     return 'come';
  //   };
  cccc = (a) => {
    return `$${a.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')}`;
  };

  // options
  gradient: boolean = true;

  colorScheme = {
    domain: [
      '#7E72A8',
      '#AE9AF5',
      '#D1F58D',
      '#A85177',
      '#F582B3',
      '#49BDFD',
      '#42D4E3',
      '#55FADC',
      '#42E398',
      '#49FD76',
    ],
  };
  constructor() {}

  ngOnInit(): void {}
  percentageFormatting(c) {
    return Math.round(c);
  }
}
