import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  AfterViewInit,
  OnChanges,
  OnDestroy,
} from '@angular/core';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';

// TERCEROS
import { NgbDateParserFormatter } from '@ng-bootstrap/ng-bootstrap';

// CLASSES
import { NgbDateCustomParserFormatter } from '../../classes/NgbDateCustomParserFormatter';

// SERVICES
import { FormatterService } from '../../services/formatter.service';

@Component({
  selector: 'app-widget-input-date',
  templateUrl: './widget-input-date.component.html',
  styleUrls: ['./widget-input-date.component.scss'],
  providers: [
    { provide: NgbDateParserFormatter, useClass: NgbDateCustomParserFormatter },
  ],
})
export class WidgetInputDateComponent
  implements OnInit, AfterViewInit, OnChanges, OnDestroy {
  @Input() fechaPreCargada?: any;
  @Input() label?: any;
  @Input() isDisabled?: any;
  @Input() dataDate?: any;
  @Input() isRequired?: boolean;
  @Output() fechaValidaEvent?: EventEmitter<any> = new EventEmitter();

  groupForm: FormGroup;

  constructor(
    private formBuilder: FormBuilder,
    private formatterService: FormatterService
  ) {}

  ngOnInit() {
    this.groupForm = this.formBuilder.group({
      genericDate: [{ value: null, disabled: false }, [Validators.required]],
    });
  }
  ngOnChanges() {
    if (this.dataDate) {
      this.parseGenericDate(this.dataDate);
    }
  }
  ngAfterViewInit() {
    this.groupForm.statusChanges.subscribe(
      (change) => {
        if (
          this.groupForm.valid ||
          (this.fc.genericDate.errors && this.fc.genericDate.errors.customError)
        ) {
          this.fechaValidaEvent.emit(
            this.formatterService.getDateFormatTimeZoneFromNgbCalendar(
              this.groupForm.value.genericDate
            )
          );
        } else {
          this.fechaValidaEvent.emit(null);
        }
      },
      (error) => {}
    );

    setTimeout(() => {
      if (this.isDisabled) {
        this.parseGenericDate(this.dataDate);
        this.groupForm.get('genericDate').disable();
        this.groupForm.get('genericDate').updateValueAndValidity();
      } else if (this.fechaPreCargada) {
        this.parseGenericDate(this.fechaPreCargada);
      } else if (this.dataDate) {
        this.parseGenericDate(this.dataDate);
      }
    }, 150);
  }

  ngOnDestroy() {}

  get fc() {
    return this.groupForm.controls;
  }

  parseGenericDate(eve?) {
    let dateForm = !eve
      ? !!this.groupForm
        ? this.groupForm.get('genericDate').value
        : null
      : eve;

    if (!!this.groupForm && typeof dateForm != 'object') {
      let objDate = this.formatterService.getDateNgbCalendar(dateForm);
      this.groupForm.get('genericDate').setValue(objDate);
      this.groupForm.get('genericDate').updateValueAndValidity();
    }
  }

  setCustomError(error) {
    let customError = error ? { customError: error } : null;
    if (
      this.fc.genericDate.errors &&
      this.fc.genericDate.errors.customError &&
      !error
    ) {
      this.groupForm.get('genericDate').setErrors(null);
    } else if (!this.fc.genericDate.errors && error) {
      this.groupForm.get('genericDate').setErrors({ customError: error });
    }
  }

  resetForm() {
    this.groupForm.get('genericDate').reset(null);
  }

  onSubmit() {}
}
