import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { CommonLayoutComponent } from './common/common-layout.component';
import { DashboardComponent } from './dashboard/dashboard.component';

const routes: Routes = [
  {
    path: '',
    redirectTo: 'dashboard',
    // redirectTo: 'cobranza/carteraVencida',

    pathMatch: 'full',
  },
  {
    path: '',
    component: CommonLayoutComponent,
    children: [
      {
        path: 'dashboard',
        component: DashboardComponent,
      },
      {
        path: 'cobranza',
        loadChildren: () =>
          import('./cobranza/cobranza.module').then((m) => m.CobranzaModule),
      },
      {
        path: 'obras',
        loadChildren: () =>
          import('./obras/obras.module').then((m) => m.ObrasModule),
      },
      {
        path: 'egresos',
        loadChildren: () =>
          import('./egresos/egresos.module').then((m) => m.EgresosModule),
      },
      {
        path: 'recursos-humanos',
        loadChildren: () =>
          import('./recursos-humanos/recursos-humanos.module').then(
            (m) => m.RecursosHumanosModule
          ),
      },
    ],
  },
  {
    path: '**',
    redirectTo: 'dashboard',
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
})
export class AppRoutingModule {}
