import { Component, OnInit, Input } from '@angular/core';
import { ChartType, ChartOptions } from 'chart.js';
import {
  SingleDataSet,
  Label,
  monkeyPatchChartJsLegend,
  monkeyPatchChartJsTooltip,
} from 'ng2-charts';
import * as modelChart from '../chartsModels';

@Component({
  selector: 'app-ng2-pie-chart',
  templateUrl: './ng2-pie-chart.component.html',
  styleUrls: ['./ng2-pie-chart.component.scss'],
})
export class Ng2PieChartComponent implements OnInit {
  @Input() params: modelChart.Ng2PieChart;

  public pieChartOptions: ChartOptions = {
    responsive: true,
  };
  public pieChartType: ChartType = 'pie';
  public pieChartLegend = true;
  public pieChartPlugins = [];

  constructor() {
    monkeyPatchChartJsTooltip();
    monkeyPatchChartJsLegend();
  }

  ngOnInit(): void {}
}
