import { Injectable } from '@angular/core';

import * as moment from 'moment';
import * as _ from 'lodash';

@Injectable({
  providedIn: 'root',
})
export class FormatterService {
  constructor() {}

  getDateFormatTimeZoneFromNgbCalendar(
    date?: {
      year: string | number;
      month: string | number;
      day: string | number;
    },
    time?: string
  ): any {
    let dateFormatted = null;
    // let valueZ = moment().format("Z");
    // let format = "YYYY-MM-DD HH:mm:ss Z";
    let format = 'YYYY-MM-DDTHH:mm:ss';

    let timeArrayString = time ? time.split(':') : ['00', '00', '00'];

    let hours = this.verificarMenorAunDigito(timeArrayString[0]);
    let minutes = this.verificarMenorAunDigito(timeArrayString[1]);
    let seconds = this.verificarMenorAunDigito(timeArrayString[2]);

    let formatDate =
      this.verificarMenorAunDigito(date.year) +
      '-' +
      this.verificarMenorAunDigito(date.month) +
      '-' +
      this.verificarMenorAunDigito(date.day);
    let formatTime = hours + ':' + minutes + ':' + seconds;

    // dateFormatted = moment(
    //   formatDate + " " + formatTime + " " + valueZ,
    //   format,
    //   true
    // )
    //   .utc()
    //   .format();

    console.log(':::::: PARSE DATE_TIME_ZONE: ', dateFormatted);

    // return dateFormatted;
    return moment(formatDate + ' ' + formatTime).format(format);
  }

  getDateNgbCalendar(date): any {
    let objDate = { year: null, month: null, day: null };
    let format = 'YYYY-MM-DDTHH:mm:ss';
    // let format = "YYYY-MM-DD HH:mm:ss Z";

    if (date && date != 'Invalid date') {
      let dateFormat = moment(date, format);

      objDate.year = Number(dateFormat.format('YYYY'));
      objDate.month = Number(dateFormat.format('MM'));
      objDate.day = Number(dateFormat.format('DD'));
    }

    return objDate;
  }

  private verificarMenorAunDigito(data) {
    return data && data.toString().length == 1 ? '0' + data : data;
  }
}
