import { Component, OnInit } from '@angular/core';
import { TemplateService } from '../shared/services/template.service';

export type HeaderType =
  | 'header-default'
  | 'header-primary'
  | 'header-info'
  | 'header-success'
  | 'header-danger'
  | 'header-dark';
export type SideNavType =
  | 'sidenav-default'
  | 'side-nav-dark'
  | 'side-nav-dark-blue';

@Component({
  selector: 'app-dashboard',
  templateUrl: './common-layout.component.html',
})
export class CommonLayoutComponent implements OnInit {
  headerThemes = [
    'header-default',
    'header-primary',
    'header-info',
    'header-success',
    'header-danger',
    'header-dark',
  ];
  sidenavThemes = ['side-nav-dark', 'side-nav-dark-blue', 'sidenav-default'];
  headerSelected: HeaderType;
  sidenavSelected: SideNavType = 'side-nav-dark-blue';
  isCollapse: boolean;
  rtlActived: boolean = false;

  themeConfigOpen: boolean = false;

  constructor(private tplSvc: TemplateService) {}

  ngOnInit() {
    this.tplSvc.isSideNavCollapseChanges.subscribe(
      (isCollapse) => (this.isCollapse = isCollapse)
    );
  }

  changeHeader(headerTheme: HeaderType) {
    this.headerSelected = headerTheme;
  }

  changeSidenav(sidenavTheme: SideNavType) {
    this.sidenavSelected = sidenavTheme;
  }

  toggleThemeConfig() {
    this.themeConfigOpen = !this.themeConfigOpen;
  }
}
